import React, { Component, useRef } from 'react';
import Card from 'react-bootstrap/Card'
import { Button, Table } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import Header from "../Header";
import Footer from "../Footer";
import { Link } from 'react-router-dom'
import { useHistory } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import ClipLoader from "react-spinners/ClipLoader";
import axios from "axios";
import { withRouter } from 'react-router-dom';
import S3 from 'react-aws-s3';
import moment from 'moment';
const dotenv = require('dotenv');
class AddCar extends Component {

    state = { userId: '', carId: 0, item: [], error: '', isLoading: false, loadingColor: '#ffffff', pageNo: 0, payment_orderId: '', carModelName: '', carType: '', carNo: '', rcBook: '' };
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.setState({ isLoading: true });
        let userId = localStorage.getItem("userId");
        this.setState({ userId: userId });
        console.log("process.env.BucketName:" + process.env.BucketName);
        //this.setState({item:this.props.match.params.data});
        this.getCars(userId);
    }

    showPopup(object) {

        this.prePayment(object);
    }
    async getCars(userId) {

        try {
            this.setState({ cabId: 0 })
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + userId + "&pageId=" + pageNo;
            let cabData = await axios.get(process.env.REACT_APP_APIURL + 'agent/get_cars?' + urlData, { headers });
            //console.log("cabData:" + JSON.stringify(cabData));
            if (cabData.data.code == 200) {
                this.setState({ item: cabData.data.data });
            } else {

            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });

    }
    addCar = async () => {
        let token = localStorage.getItem("token");

        let pageNo = this.state.pageNo + 1;
        const headers = { 'Authorization': `Bearer ${token}` };
        let carModelNo = '';
        let carNo = '';
        if (this.state.carModelName == "" || this.state.carModelName == null) {
            this.setState({ error: "Please enter car model name" });
            return false;
        }
        if (this.state.carNo == "" || this.state.carNo == null) {
            this.setState({ error: "Please enter car number" })
            return false;
        }

        if (this.state.carType == "" || this.state.carType == null) {
            this.setState({ error: "Please enter car type" })
            return false;
        }
        if (this.state.rcBook == "" || this.state.rcBook == null) {
            this.setState({ error: "Please enter car RC" })
            return false;
        }
        this.setState({ isLoading: true });

        let urlData = "&userId=" + this.state.userId + "&carModelNo=" + this.state.carModelName + "&carNo=" + this.state.carNo + "&carType=" + this.state.carType + "&rcBook=" + this.state.rcBook;
        //const response = await fetch('http://localhost:3001/booking/getCabs?originObj='+originObj+'&destinationObj='+destinationObj, { headers });

        const response = await fetch(process.env.REACT_APP_APIURL + 'agent/add_car?' + urlData, { headers });

        const dataRes = await response.json();

        if (dataRes.code == 200) {

            this.setState({ error: 'Car added successfully' });
            this.setState({carModelName:''});
            this.setState({carType:''});
            this.setState({carNo:''});
            this.setState({rcBook:''});
            
            this.getCars(this.state.userId);
        } else {
            console.log("errorr")
            this.setState({ error: 'some internal error please try later' })
        }
        this.setState({ isLoading: false });
    }
    setCarModelName = (carModelName) => {
        this.setState({ carModelName: carModelName.target.value });
    }
    setCarNo = (carNo) => {
        this.setState({ carNo: carNo.target.value });
    }
    setCarType = (carType) => {
        this.setState({ carType: carType.target.value });
    }
    setRcBook = (event) => {
        let fileData = event.target.files[0];
        let fileSize = fileData.size;
        let fileName = fileData.name;
        let ext = fileName.split('.').pop();
        let fileName_new = moment.now() + "." + ext;
        const env = dotenv.config().parsed;
        console.log("fileName=" + fileName_new);

        if (fileSize > 110000) {
            this.setState({ error: 'Image size must be <100kb' });
            this.setState({ rcBook: '' });
            return false;
        }

        this.setState({ isLoading: true });
       // console.log("process.env.BucketName:" + process.env.BucketName);
        const config = {
            bucketName: process.env.REACT_APP_BUCKET_NAME,
                dirName: 'drivers', /* optional */
                region: process.env.REACT_APP_REGION,
                accessKeyId: process.env.REACT_APP_S3ACCESS_KEY,
                secretAccessKey: process.env.REACT_APP_S3SECRET_KEY,
        }
        const ReactS3Client = new S3(config);
        const fileUrl = process.env.REACT_APP_CDN_URL + "drivers/" + fileName;
        console.log("fileUrl:"+fileUrl);
        ReactS3Client
            .uploadFile(fileData, fileName)
            .then(data => {  this.setState({ rcBook: fileUrl }) })
            .catch(err => { this.setState({ error: 'File Not uploaded, Please try again' }); })
        //this.setState({ rcBook: rcBook.target.value });

        this.setState({ isLoading: false });
    }
    deleteCar = async (carId) => {
        try {
            this.setState({ cabId: 0 })
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + this.state.userId + "&carId=" + carId;
            let cabData = await axios.delete(process.env.REACT_APP_APIURL + 'agent/car?' + urlData, { headers });
            console.log("cabData:" + JSON.stringify(cabData.data));
            if (cabData.data.code == 200) {
                this.setState({ error: 'Car Deleted' });
                this.getCars(this.state.userId);
            } else {

            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });

    }
    getCarByID = async (carId) => {

        try {
            this.setState({ cabId: 0 })
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + this.state.userId + "&carId=" + carId;
            let cabData = await axios.get(process.env.REACT_APP_APIURL + 'agent/get_carbyid?' + urlData, { headers });
            console.log("cabData:" + JSON.stringify(cabData.data));
            if (cabData.data.code == 200) {
                this.setState({ carId: cabData.data.data._id });
                this.setState({ carModelName: cabData.data.data.carModelName });
                this.setState({ carType: cabData.data.data.carType });
                this.setState({ carNo: cabData.data.data.carNo });
                this.setState({ rcBook: cabData.data.data.rcBook });

            } else {

            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });

    }

    editCar = async () => {
        try {

            if (this.state.carId == "" || this.state.carId == null) {
                this.setState({ error: "Please Select car first" });
                return false;
            }
            if (this.state.carModelName == "" || this.state.carModelName == null) {
                this.setState({ error: "Please enter car model name" });
                return false;
            }
            if (this.state.carNo == "" || this.state.carNo == null) {
                this.setState({ error: "Please enter car number" })
                return false;
            }

            if (this.state.carType == "" || this.state.carType == null) {
                this.setState({ error: "Please enter car type" })
                return false;
            }
            if (this.state.rcBook == "" || this.state.rcBook == null) {
                this.setState({ error: "Please enter car RC" })
                return false;
            }
            this.setState({ isLoading: true });
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = {
                userId: this.state.userId,
                carModelNo: this.state.carModelName,
                carNo: this.state.carNo,
                carType: this.state.carType,
                rcBook: this.state.rcBook,
                carId: this.state.carId
            }
            let cabData = await axios.put(process.env.REACT_APP_APIURL + 'agent/update_car?', urlData, { headers });
            console.log("cabData:" + JSON.stringify(cabData.data));
            if (cabData.data.code == 200) {
                this.setState({ error: 'Car updated' });
                this.getCars(this.state.userId);
            } else {
                this.setState({ error: 'Something went wrong' });
            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });

    }
    render() {
        const override = `
        display: block;
        margin: 0 auto;
        border-color: red;
        position: absolute;
        z-index: 9;
        margin: 0px auto;
        left: 40%;
        `;
        return (

            <div>
                <Header />
                <div>
                    <ClipLoader css={override} style={{ borderColor: 'red' }} color={this.state.loadingColor} loading={this.state.isLoading} size={150} >  </ClipLoader>
                </div>


                <div>
                    <section id="pricing" className="pricing">

                        <div className="container" data-aos="fade-up" style={{ width: '95%!important' }}>

                            <div className="row">
                                <div className="col-lg-12 col-md-12">

                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>Add Car

                                        </Card.Title>
                                        <Card.Body>
                                            <div style={{ color: 'red' }}>
                                                {this.state.error}
                                            </div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Car Model Name <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="carModelName" type="text" placeholder="Car Model Name`" value={this.state.carModelName} onChange={this.setCarModelName} />
                                                    </Form.Group>
                                                </div>
                                                <div >
                                                    <Form.Group controlId="formBasicEmail">
                                                        <Form.Label>Car No.<spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="carNo" type="text" placeholder="Car No." value={this.state.carNo} onChange={this.setCarNo} />
                                                    </Form.Group>
                                                </div>
                                            </div>

                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Type <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="carType" type="text" placeholder="sedan, compact, luxury" value={this.state.carType} onChange={this.setCarType} />
                                                    </Form.Group>
                                                </div>
                                                <div >
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>RC Upload<spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control type="file" onChange={this.setRcBook} placeholder="Select RC" />
                                                    </Form.Group>
                                                    <a href={this.state.rcBook} target="_blank"> <img src={this.state.rcBook} width={100} /></a>
                                                </div>
                                                
                                            </div>
                                            <div className="col-12">
                                                {this.state.carId < 1 ? <Form.Group controlId="formBasicEmail" style={{ float: 'right' }}>
                                                    <Button variant="primary" type="button" onClick={this.addCar.bind(this)}>
                                                        Add Car
                                                    </Button>
                                                </Form.Group> : <Form.Group controlId="formBasicEmail" style={{ float: 'right' }}>
                                                    <Button variant="primary" type="button" onClick={this.editCar.bind(this)}>
                                                        Update Car
                                                    </Button>
                                                </Form.Group>
                                                }

                                            </div>
                                        </Card.Body>
                                    </Card>
                                </div>

                            </div>
                            <br />
                            <div className="row">
                                <div className="col-lg-12 col-md-12">

                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>My Cars </Card.Title>
                                        <Card.Body>
                                            <div style={{ color: 'red' }}>
                                                <Table striped bordered hover responsive>
                                                    <thead>
                                                        <tr>
                                                            <th>Car model</th>
                                                            <th>Number </th>
                                                            <th>Type</th>
                                                            <th>Rc</th>
                                                            <th>Approval</th>
                                                            <th>Action</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {
                                                            this.state.item.map((object, i) => {
                                                                return <tr key={i + 1}>
                                                                    <td>{object.carModelName}</td>
                                                                    <td>{object.carNo}</td>
                                                                    <td>{object.carType}</td>
                                                                    <td> <a
                                                                        href={object.rcBook} target="_blank"> <img src={object.rcBook} width={100} /></a></td>

                                                                    <td>{object.status}</td>
                                                                    <td>
                                                                        <Button style={{ float: 'right', marginRight: 5 }} variant="primary" type="button" onClick={this.deleteCar.bind(this, object._id)}>
                                                                            <i className="fa fa-remove iconStyle" aria-hidden="true"></i>
                                                                        </Button>&nbsp;&nbsp;
                                                                        <Button style={{ float: 'right', marginRight: 5 }} variant="primary" type="button" onClick={this.getCarByID.bind(this, object._id)}>
                                                                            <i className="fa fa-eye iconStyle" aria-hidden="true"></i>
                                                                        </Button>

                                                                    </td>
                                                                </tr>;
                                                            })
                                                        }

                                                    </tbody>
                                                </Table>
                                            </div>

                                        </Card.Body>
                                    </Card>
                                </div>

                            </div>

                        </div>

                    </section>


                </div>
            </div>
        );
    }
}

export default withRouter(AddCar);